.page {
    min-width: 320px;
    min-height: 100vh;
    background-color: #000;
    color: #FFF;
    font-family: 'Inter', 'Arial', sans-serif;
    margin: 0 auto;

    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    text-rendering: optimizeLegibility;
}